<template>
  <div>
    <div class="row mr-0 mt-1">
      <va-checkbox :label="$t('placeholder.showTrafic')" v-model="showTrafic" />

    </div>
    <div class="google-map map-fill-height mb-3" ref="googleMap" style="height: 57vh;" />
  </div>
</template>

<script>
import * as GoogleMapsLoader from 'google-maps'
import * as spinners from 'epic-spinners'
import axios from 'axios'

export default {
  name: 'google-map',
  data () {
    return {
      distance: '',
      routes: [],
      routesTable: [],
      routsSelect: {
        value: [],
        options: [],
      },
      departureTime: null,
      iconPr: 'https://i.ibb.co/zScnP9L/IconPr.gif',
      iconSc: 'https://i.ibb.co/4t5K1jd/IconSec.gif',
      iconA: '',
      iconB: '',
      loading: true,
      googleMap: null,
      smap: null,
      showTrafic: false,
      showPrimariy: true,
      showsec: true,
      trafficLayer: null,
      markers: [],
      IW: null,
      alert: 'https://dm0qx8t0i9gc9.cloudfront.net/previews/audio/BsTwCwBHBjzwub4i4/car-horn-3_Gy26a2Nd_NWM.mp3',

    }
  },
  components: {
    ...spinners,
  },
  async mounted () {
    await this.PageRender()
    let i = 0
    setInterval(() => {
      this.ClaearAllToasts()
      i += 1
      this.showToast(`بدء تحرك حافلة الخط ${i}`, {
        icon: 'fa-exclamation',
        position: 'bottom-left',
        duration: null,
        type: 'error',
        action: [
          {
            icon: 'fa-times',
            onClick: (e, toastObject) => {
              toastObject.goAway(0)
            },
          }],
      })
      new Audio(this.alert).play()
    }, 60000)
    let j = 0
    setInterval(() => {
      this.ClaearAllToasts()

      j += 1
      this.showToast(`تم وصول حافلة الخط ${j}`, {
        icon: 'fa-exclamation',
        position: 'bottom-left',
        duration: null,
        type: 'error',
        action: [
          {
            icon: 'fa-times',
            onClick: (e, toastObject) => {
              toastObject.goAway(0)
            },
          }],
      })
      new Audio(this.alert).play()
    }, 150000)
  },
  methods: {
    async PageRender () {
      const self = this
      GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
      GoogleMapsLoader.VERSION = '3.35'
      await GoogleMapsLoader.load(async (google) => {
        this.googleMap = google
        self.smap = new google.maps.Map(this.$refs.googleMap, {
          center: new google.maps.LatLng(24.67451093889048, 46.684984545094366),
          zoom: 12,
          mapTypeId: google.maps.MapTypeId.ROADMAP,
        })
        self.IW = new google.maps.InfoWindow()
        self.trafficLayer = new google.maps.TrafficLayer()
        self.iconA = {
          url: 'http://maps.google.com/mapfiles/kml/paddle/A.png', // url
          scaledSize: new google.maps.Size(35, 35), // scaled size
        }
        self.iconB = {
          url: 'http://maps.google.com/mapfiles/kml/paddle/B.png', // url
          scaledSize: new google.maps.Size(35, 35), // scaled size
        }
        var LongLat = '24.597129445118107, 46.746849552066976'.split(',')
        var org = new google.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
        var LongLatDes = '24.753852667049653, 46.66401329841596'.split(',')
        var destination = new google.maps.LatLng(
          Number(LongLatDes[0]),
          Number(LongLatDes[1]),
        )
        var wh = ['24.597987666454245, 46.7432013847375',
          '24.63461150500481, 46.71706498211887',
          '24.65423086981132, 46.71076917976442',
          '24.661657803678626, 46.71213867579173',
          // '24.66150720899302, 46.712110082221294',
          '24.664994211584943, 46.708272106493915',
          '24.681185125840013, 46.70084895170687',
          '24.681768491908898, 46.69125190514781',
          '24.704255668887896, 46.67991239581891',
          '24.733903345404862, 46.66497447113631']
        const st = [
          {
            loc: '24.59627244594546, 46.73592018493405',
            name: 'Al Batha 210',
          },
          {
            loc: '24.59931894275448, 46.73429910064909',
            name: 'Al Batha 208',
          }, {
            loc: '24.60779218478512, 46.730116855104754',
            name: 'Al Batha 207',
          }, {
            loc: '24.627416427622524, 46.71927304863761',
            name: 'Al Batha 204',
          }, {
            loc: '24.634941994425773, 46.71723041312731',
            name: 'Al Batha 202',
          }, {
            loc: '24.644197082559483, 46.7162774750187',
            name: 'Al Batha 201',
          }, {
            loc: '24.652579105969387, 46.712741673815465',
            name: 'Al-Muraba 610',
          }, {
            loc: '24.660551287117332, 46.712603300181065',
            name: 'Al-Muraba 611',
          }, {
            loc: '24.665335993932153, 46.708905476925615',
            name: 'Al-Muraba 602',
          }, {
            loc: '24.665966632184304, 46.707577530411044',
            name: 'Al-Muraba 606',
          }, {
            loc: '24.670156938117824, 46.70590542481681',
            name: 'Al-Muraba 605',
          }, {
            loc: '24.681005074449537, 46.70096383470556',
            name: 'Al-Olaya 613',
          }, {
            loc: '24.681705485323, 46.69128916939727',
            name: 'Olaya 218',
          }, {
            loc: '24.685365216511087, 46.68953140273582',
            name: 'Olaya 217',
          }, {
            loc: '24.689461132607526, 46.68746194708449',
            name: ' Olaya 216',
          }, {
            loc: '24.695108990115735, 46.68460993865002',
            name: 'Olaya 212',
          }, {
            loc: '24.70339459698896, 46.680393455312874',
            name: 'Olaya 211',
          }, {
            loc: '24.710529101791384, 46.676831539857986',
            name: 'Olaya 210',
          }, {
            loc: '24.699066741172036, 46.68258224302117',
            name: 'Olaya 209',
          }, {
            loc: '24.71798894135059, 46.672985070760795',
            name: 'Olaya 208',
          }, {
            loc: '24.728219888538923, 46.667884995143666',
            name: 'Olaya 207',
          }, {
            loc: '24.740351051694358, 46.66175421708246',
            name: 'Olaya 206',
          },
        ]
        var waypoints = []
        const station = {
          url: 'https://cdn-icons-png.flaticon.com/512/6395/6395324.png', // url
          scaledSize: new google.maps.Size(35, 35), // scaled size
        }
        wh.forEach((point, i) => {
          const LonLat = point.split(',')
          waypoints.push({ latitude: Number(LonLat[0]), longitude: Number(LonLat[1]) })
        })
        st.forEach((point, i) => {
          const LonLat = point.loc.split(',')
          const mo = new google.maps.Marker({
            position: new google.maps.LatLng(Number(LonLat[0]), Number(LonLat[1])),
            map: self.smap,
            title: point.name,
            animation: google.maps.Animation.DROP,
            icon: station,
          })
          self.markers.push(mo)
        })
        axios
          .post(
            'https://map-api-ubzex7qxpa-ew.a.run.app/routes/computeRouteWithTrafficLength',
            {
              origin: org,
              destination: destination,
              waypoints: waypoints,
            },
          )
          .then((resp) => {
            self.render(resp.data[0], waypoints)
            const mo = new google.maps.Marker({
              position: new google.maps.LatLng(org),
              map: self.smap,
              title: 'Riyadh, Transportation Center A, Riyadh',
              animation: google.maps.Animation.DROP,
              icon: self.iconA,
            })
            mo.addListener('click', (eve) => {
              self.showIW(resp.data[0], eve.latLng)
            })
            self.markers.push(mo)
            const md = new this.googleMap.maps.Marker({
              position: new this.googleMap.maps.LatLng(destination),
              map: this.smap,
              title: 'Al-Mughera Bin Shoubah 101, Riyadh',
              animation: this.googleMap.maps.Animation.DROP,
              icon: self.iconB,
            })
            md.addListener('click', (eve) => {
              self.showIW(resp.data[0], eve.latLng)
            })
            this.markers.push(md)
          })
      })
    },
    render (route) {
      const self = this
      if (!route) {
        return
      }
      const decodedPath = route.polyline.geoJsonLinestring.coordinates
      const decodedPathLatLng = decodedPath.map((elt) => {
        return { lat: elt[1], lng: elt[0] }
      })
      const speedReadingIntervals = route.travelAdvisory?.speedReadingIntervals || []

      speedReadingIntervals.forEach((interval) => {
        // console.log(`interval`, interval);
        const startIndex = interval.startPolylinePointIndex || 0
        const endIndex = interval.endPolylinePointIndex || 0
        const type = interval.speed
        let poly = decodedPathLatLng.slice(startIndex, endIndex + 1)
        var colors = { TRAFFIC_JAM: '#d74342', SLOW: '#e69e01', NORMAL: '#4078c9' }
        poly = new this.googleMap.maps.Polyline({
          path: poly,
          strokeColor: colors[type],
          geodesic: true,
          strokeOpacity: 1,
          strokeWeight: 6,
        })
        this.googleMap.maps.event.addListener(poly, 'click', function (evt) {
          self.showIW(route, evt.latLng)
        })
        poly.setMap(self.smap)
      })
    },
    showIW (ApiData, position) {
      const data = ApiData
      let html = ''
      html += '<div dir="ltr">'
      html += `<span class="${'infotitlePr'}">${
        'خط النسيم الغربي حسان ابن ثابت'
      }</span><br/>`
      html += `<span class="infosegment">${this.$t('distance')}: ${
        data.statistics.distance.value / 1000
      } ${this.$t('KM')} </span><br/>`
      html += `<span class="infosegment"> ${this.$t('TimeWithoutCrowding')}: ${Math.round(
        1500 / 60,
      )} ${this.$t('minuits')}</span><br/>`
      html += `<span class="infosegment"> ${this.$t('timeNow')}: ${this.fromatTime(
        data.statistics.duration.value,
        true,
      )}</span><br/>`

      html += '<br/>'
      html += `<span class="infonormal">${this.$t('normal')}: ${
        data.statistics.normal.distance.value / 1000
      } ${this.$t('KM')} (${data.statistics.normal.distance.percentage})</span><br/>`
      html += `<span class="infoslow">${this.$t('Slow')}: ${
        data.statistics.slow.distance.value / 1000
      } ${this.$t('KM')}  (${data.statistics.slow.distance.percentage})</span><br/>`
      html += `<span class="infojam">${this.$t('crowded')}: ${
        data.statistics.jam.distance.value / 1000
      } ${this.$t('KM')}  (${data.statistics.jam.distance.percentage})</span><br/>`
      html += '</div>'

      if (this.IW) {
        this.IW.setContent(html)
        this.IW.setPosition(position)
        this.IW.open({ map: this.smap })
      }
    },
    fromatTime (time, isSec = false) {
      let text

      const durationInSec = isSec ? parseInt(time) : parseInt(time) * 60

      if (durationInSec < 60) text = `${durationInSec}${this.$t('second')}`
      text = `${Math.round(durationInSec / 60)} ${this.$t('minuits')}`
      return text
    },
    renderAll () {
      const self = this
      this.routes.forEach((el) => {
        if (el.poly) {
          el.poly.forEach((stepPoly) => {
            // eslint-disable-next-line no-unused-expressions
            stepPoly?.setMap(this.smap)
          })
        }
        var LongLat = el.FromLocation.split(',')
        var org = new this.googleMap.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
        // var LongLatDes = el.toLocation.split(',')
        // var destination = new this.googleMap.maps.LatLng(
        //   Number(LongLatDes[0]),
        //   Number(LongLatDes[1]),
        // )
        const mo = new this.googleMap.maps.Marker({
          position: new this.googleMap.maps.LatLng(org),
          map: this.smap,
          title: el.RouteName,
          animation: this.googleMap.maps.Animation.DROP,
          icon: self.iconA,
        })
        mo.addListener('click', (eve) => {
          self.showIW(el, eve.latLng)
        })

        this.markers.push(mo)
        // eslint-disable-next-line no-new
        // const md = new this.googleMap.maps.Marker({
        //   position: new this.googleMap.maps.LatLng(destination),
        //   map: this.smap,
        //   title: el.RouteName,
        //   animation: this.googleMap.maps.Animation.DROP,
        //   icon: self.iconB,
        // })
        // md.addListener('click', (eve) => {
        //   self.showIW(el, eve.latLng)
        // })
        // this.markers.push(md)
      })
      this.recenter()
    },
    DeletAll () {
      this.routes.forEach((el) => {
        if (el.poly) {
          el.poly.forEach((stepPoly) => {
            // eslint-disable-next-line no-unused-expressions
            stepPoly?.setMap(null)
          })
        }
      })
      this.markers.forEach((m) => {
        // eslint-disable-next-line no-unused-expressions
        m?.setMap(null)
      })
      this.recenter()
    },
    drawOnlyRoute (route) {
      const self = this

      if (route.poly) {
        for (let i = 0; i < route.poly.length; i++) {
          route.poly[i].setMap(this.smap)
        }
        var LongLat = route.FromLocation.split(',')
        var org = new this.googleMap.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
        // var LongLatDes = route.toLocation.split(',')
        // var destination = new this.googleMap.maps.LatLng(
        //   Number(LongLatDes[0]),
        //   Number(LongLatDes[1]),
        // )
        const mo = new this.googleMap.maps.Marker({
          position: new this.googleMap.maps.LatLng(org),
          map: this.smap,
          title: route.RouteName,
          animation: this.googleMap.maps.Animation.DROP,
          icon: self.iconA,
        })
        mo.addListener('click', (eve) => {
          self.showIW(route, eve.latLng)
        })
        this.markers.push(mo)
        // eslint-disable-next-line no-new
        // const md = new this.googleMap.maps.Marker({
        //   position: new this.googleMap.maps.LatLng(destination),
        //   map: this.smap,
        //   title: route.RouteName,
        //   animation: this.googleMap.maps.Animation.DROP,
        //   icon: self.iconB,
        // })
        // md.addListener('click', (eve) => {
        //   self.showIW(route, eve.latLng)
        // })
        // this.markers.push(md)
        this.recenter()
      }
    },
    showRouteModal (id) {
      this.routsSelect.value = [id]
      const position = document.getElementsByClassName('google-map')[0].offsetTop
      // smooth scroll
      document
        .getElementsByTagName('main')[0]
        .scrollTo({ top: position, behavior: 'smooth' })
      this.recenter()
    },
    recenter () {
      this.smap.panTo(new this.googleMap.maps.LatLng(24.67451093889048, 46.684984545094366))
      this.smap.setZoom(13)
    },
  },
  watch: {
    showTrafic: function () {
      if (this.trafficLayer != null) {
        this.showTrafic
          ? this.trafficLayer.setMap(this.smap)
          : this.trafficLayer.setMap(null)
      }
    },
    showPrimariy: function () {
      this.DeletAll()
      if (this.showsec && this.showPrimariy) {
        this.routsSelect.value = []
      } else if (!this.showsec && this.showPrimariy) {
        this.DeletAll()
        const newRoutes = []
        this.routes.forEach((route) => {
          if (route.iSPrimary) {
            newRoutes.push(route.id)
          }
        })
        if (newRoutes.length > 0) {
          this.routsSelect.value = newRoutes
        }
      } else if (this.showsec && !this.showPrimariy) {
        this.DeletAll()
        const newRoutes = []
        this.routes.forEach((route) => {
          if (!route.iSPrimary) {
            newRoutes.push(route.id)
          }
        })
        if (newRoutes.length > 0) {
          this.routsSelect.value = newRoutes
        }
      } else {
        this.routsSelect.value = []
      }
    },
    showsec: function () {
      this.DeletAll()
      if (this.showsec && this.showPrimariy) {
        this.routsSelect.value = []
      } else if (!this.showsec && this.showPrimariy) {
        this.DeletAll()
        const newRoutes = []
        this.routes.forEach((route) => {
          if (route.iSPrimary) {
            newRoutes.push(route.id)
          }
        })
        if (newRoutes.length > 0) {
          this.routsSelect.value = newRoutes
        }
      } else if (this.showsec && !this.showPrimariy) {
        this.DeletAll()
        const newRoutes = []
        this.routes.forEach((route) => {
          if (!route.iSPrimary) {
            newRoutes.push(route.id)
          }
        })
        if (newRoutes.length > 0) {
          this.routsSelect.value = newRoutes
        }
      } else {
        this.routsSelect.value = []
      }
    },
    routes: function () {
      this.routsSelect.options = this.routes
    },
    'routsSelect.value': function () {
      if (this.routsSelect.value.length > 0) {
        this.DeletAll()
        this.routsSelect.value.forEach((id) => {
          console.log(id)
          const r = this.routes.find((x) => x.id === id)
          this.drawOnlyRoute(r)
        })
      } else {
        this.DeletAll()
        this.renderAll()
      }
      console.log('routsSelect.value change', this.routsSelect.value)
    },
  },
}
</script>

<style lang="scss">

