  background-position: 0 -35px;
}

i.fa.fa-exclamation {
  color: red !important;
}

.va-modal__inner {
  max-width: 100%;
}

.unitsNames-width {
  max-width: 177px;
}

#sidebar {
  flex-basis: 15rem;
  flex-grow: 1;
  padding: 1rem;
  max-width: 30rem;
  height: 69vh;
  box-sizing: border-box;
  overflow: auto;
}

#map {
  flex-basis: 0;
  flex-grow: 4;
